#container {
	text-align: center;
	width: 800px;
	margin-left: auto;
	margin-right: auto;
	margin-top: 150px;
}

#timer {
	font-size: 62px;
	border: 1px solid rgb(230, 225, 225);
	padding-left: 50px;
	padding-right: 50px;
	margin-bottom: 40px;
}

#resetButton {
	margin-left: 30px;
	height: 45px;
	width: 70px;
	background-color: #f5f5f5;
	border: 1px solid #dcdcdc;
}

#startStopButton {
	color: white;
	height: 45px;
	width: 70px;
	background-color: #4d90fe;
	border: 1px solid #3079ed;
}
